<!--eslint-disable-->
<template>
  <div class="destaques-wrapper">
    <div id="destaques">
      <div class="slider-wrapper">
        <div class="slider">
          <div
            class="card"
            :class="{'active': isActive, 'is-visible': f.is_visible == 1}"
            v-for="f in formacoes"
            :key="f.id"
            :data-card-id="f.id"
          >
            <div class="card-img" :style="{'background-image': 'url(' + f.img + ')'}"></div>
            <div class="card-content">
              <div class="card-header">
                <div class="card-type">{{f.categoria}}</div>
                <div
                  class="card-status"
                  :class="{'is-full': f.is_full == 1, 'has-vagas': f.last_vagas == 1}"
                >
                  <span :class="{'is-full': f.is_full == 1}">vagas preenchidas</span>
                  <span :class="{'has-vagas': f.last_vagas == 1}">últimas vagas</span>
                </div>
              </div>
              <div class="card-body">
                <h3 class="card-title">{{f.title}}</h3>
                <div class="card-date">
                  <router-link :to="f.url">
                    <div class="card-cta">
                      <div class="cta-txt">Saiba mais</div>
                    </div>
                  </router-link>
                  <div class="txt">
                    {{f.start}}
                    <br />
                    {{f.end}}
                  </div>
                  <div class="icon" :style="{'background-image': 'url(' + calendar + ')'}"></div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="indicators">
          <div
            class="ind"
            :class="{'active': isActive}"
            v-for="f in formacoes"
            :key="f.id"
            @click="currentSlide(f.id)"
          ></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";
import calendar from "../../assets/calendar.svg";
import moment from "moment";

export default {
  data() {
    return {
      calendar,
      isActive: true,
      index: 1,
      formacoes: [
        {
          id: "",
          code: "",
          title: "",
          text: "",
          url: "",
          img: "",
          start: "",
          end: "",
          is_full: "",
          last_vagas: "",
          is_visible: "",
          categoria: "",
        },
      ],
    };
  },
  methods: {
    showDestaque(n) {
      var i;
      var slides = document.querySelectorAll(".slider .card.is-visible");
      var dots = document.querySelectorAll(".indicators .ind");
      if (n > slides.length) {
        this.index = 1;
      }
      if (n < 1) {
        this.index = slides.length;
      }
      for (i = 0; i < slides.length; i++) {
        slides[i].className = "card is-visible";
      }
      for (i = 0; i < dots.length; i++) {
        dots[i].className = dots[i].className.replace(" active", "");
        this.isActive = false;
      }
      slides[this.index - 1].className = "card is-visible active";
      dots[this.index - 1].className += " active";
      this.isActive = true;
    },
    prevSlides(n) {
      this.showDestaque((this.index -= n));
    },
    plusSlides(n) {
      this.showDestaque((this.index += n));
    },
    currentSlide(n) {
      this.showDestaque((this.index = n));
    },
  },
  mounted() {
    var vm = this;
    // this.showDestaque(this.index);

    var slider = document.querySelector(".slider");
    var slides = document.querySelectorAll(".slider .card.is-visible");

    if (window.innerWidth < 600) {
      slider.addEventListener("touchstart", handleTouchStart, false);
      slider.addEventListener("touchmove", handleTouchMove, false);

      var xDown = null;
      var yDown = null;

      function getTouches(evt) {
        return evt.touches || evt.originalEvent.touches;
      }

      function handleTouchStart(evt) {
        const firstTouch = getTouches(evt)[0];
        xDown = firstTouch.clientX;
        yDown = firstTouch.clientY;
      }

      function handleTouchMove(evt) {
        if (!xDown || !yDown) {
          return;
        }

        var xUp = evt.touches[0].clientX;
        var yUp = evt.touches[0].clientY;

        var xDiff = xDown - xUp;
        var yDiff = yDown - yUp;

        var active = document.querySelector(".slider .card.is-visible.active");
        var id = active.getAttribute("data-card-id");

        if (Math.abs(xDiff) > Math.abs(yDiff)) {
          /*most significant*/
          if (xDiff > 0) {
            /* left swipe */
            vm.plusSlides(1);
          } else {
            /* right swipe */
            vm.prevSlides(1);
          }
        } // else {
        // if (yDiff > 0) {
        //   /* up swipe */
        // } else {
        //   /* down swipe */
        // }
        // }
        /* reset values */
        xDown = null;
        yDown = null;
      }
    }

    var map = Array.prototype.map;
    axios
      .get("https://beta.anacarolinapereira.pt/db-api/getFormacaoInfo.php")
      .then((resp) => {
        const data = resp.data;
        var formacoesArray = [];

        map.call(data, function (f) {
          if (f.is_destaque == 1) {
            var src = f.img_src.split("uploads");
            var ds;
            var de;
            if (f.date_start == "0000-00-00") {
              ds = "disponível brevemente";
              de = "";
            } else {
              ds = moment(f.date_start).format("DD-MM-YYYY");
              de = moment(f.date_end).format("DD-MM-YYYY");
            }

            var categoria;
            if (f.categoria_id == 1) categoria = "formacao";
            else if (f.categoria_id == 2) categoria = "curso";
            else categoria = "webinar";

            var categoria_title;
            if (f.categoria_id == 1) categoria_title = "formação";
            else if (f.categoria_id == 2) categoria_title = "curso";
            else categoria_title = "webinar";

            formacoesArray.push({
              id: f.id,
              code: f.code,
              title: f.title,
              text: f.text,
              url: "/academia/" + categoria + f.link,
              img: "uploads" + src[1],
              start: ds,
              end: de,
              is_full: f.is_full,
              last_vagas: f.last_vagas,
              is_visible: f.is_visible,
              categoria: categoria_title,
            });
          }
        });

        vm.$data.formacoes = formacoesArray;
      })
      .then(() => {
        vm.showDestaque(this.index);

        var active = document.querySelector(".slider .card.is-visible");

        if (active.length > 1) {
          setInterval(() => {
            this.plusSlides(this.index);
          }, 5000);
        }
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(130);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.destaques-wrapper {
  background-color: #52c3d5;
  height: pxToEm(600);
  padding: $pd-desktop;
  display: flex;
  align-items: center;
  justify-content: center;

  @media only screen and (min-width: 1200px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (max-width: 820px) {
    height: pxToEm(800);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
  }

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    height: pxToEm(700);
    padding: 0 pxToEm(20);
  }

  #destaques {
    width: 100%;
    height: 100%;
    display: flex;
    margin: $mg-desktop;
    align-items: center;
    justify-content: center;

    @media only screen and (min-width: 300px) and (max-width: 599px) {
      // height: auto;
      justify-content: center;
      margin: 0 pxToEm(20);
    }

    .slider-wrapper {
      //   max-width: pxToEm(960);
      width: 100%;
      //   max-height: pxToEm(120);
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      //   margin-bottom: pxToEm(60);
      justify-content: space-around;

      @media only screen and (min-width: 300px) and (max-width: 599px) {
        // height: auto;
        // max-height: 100%;
        // margin-bottom: pxToEm(10);
        justify-content: space-between;
        align-items: center;
      }

      .slider {
        width: 100%;
        height: 70%;
        // display: flex;
        // margin-top: pxToEm(40);

        @media only screen and (max-width: 820px) {
          margin: 0;
          justify-content: center;
        }

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          //   height: pxToEm(120);
          margin-top: pxToEm(40);
          height: 75%;
        }

        .card {
          width: pxToEm(1400);
          height: pxToEm(500);
          margin: auto;
          display: none;
          flex-direction: column;
          align-items: center;
          justify-content: space-between;
          cursor: pointer;
          transition: 0.5s;
          opacity: 0;
          // position: relative;
          position: absolute;
          max-width: pxToEm(1400);
          width: 100%;
          left: 50%;
          transform: translateX(-50%);

          @media only screen and (max-width: 820px) {
            width: pxToEm(500);
            height: pxToEm(530);
            background-color: $white;
            border-radius: pxToEm(43);
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            cursor: pointer;
            transition: 0.5s;
            margin: pxToEm(50) pxToEm(10) pxToEm(50) pxToEm(10);
            box-shadow: pxToEm(3) pxToEm(3) pxToEm(15) rgba(0, 0, 0, 0.2);
          }

          @media only screen and (min-width: 300px) and (max-width: 599px) {
            width: 90%;
            margin: 0;
          }

          &.active {
            opacity: 1;
          }

          &.is-visible {
            display: flex;

            // @media only screen and (max-width: 769px) {
            //   display: none;
            // }
          }

          .card-img {
            background-color: #cbcbcb;
            border-radius: pxToEm(33);
            width: pxToEm(530);
            height: pxToEm(360);
            background-size: cover;
            background-position: center;
            position: absolute;
            left: 0;
            z-index: 1;
            top: 50%;
            transform: translateY(-50%);

            @media only screen and (min-width: 992px) and (max-width: 1199px) {
              //   width: calc(500px - 10%);
              height: calc(280px - 10%);
              width: 41%;
            }

            @media only screen and (min-width: 821px) and (max-width: 991px) {
              //   width: calc(500px - 16%);
              height: calc(280px - 16%);
              width: 41%;
            }

            @media only screen and (max-width: 820px) {
              background-color: #cbcbcb;
              border-radius: pxToEm(43);
              width: 100%;
              height: 70%;
              background-size: cover;
              background-position: center;
              position: relative;
              left: unset;
              z-index: unset;
              top: unset;
              transform: unset;
            }
          }

          .card-content {
            position: absolute;
            width: pxToEm(900);
            height: pxToEm(370);
            background-color: $white;
            border-radius: pxToEm(43);
            box-shadow: pxToEm(3) pxToEm(3) pxToEm(15) rgba(0, 0, 0, 0.2);
            display: flex;
            flex-direction: column;
            padding: pxToEm(30) pxToEm(30);
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            // justify-content: space-between;

            @media only screen and (min-width: 992px) and (max-width: 1199px) {
              //   width: calc(670px - 10%);
              height: calc(280px - 10%);
              width: 61%;
            }

            @media only screen and (min-width: 821px) and (max-width: 991px) {
              //   width: calc(670px - 16%);
              height: calc(280px - 16%);
              width: 61%;
            }

            @media only screen and (max-width: 820px) {
              display: flex;
              flex-direction: column;
              padding: pxToEm(30) pxToEm(30);
              height: 30%;
              position: relative;
              width: 88%;
              left: unset;
              z-index: unset;
              top: unset;
              transform: unset;
              box-shadow: none;
              border-radius: none;
            }

            @media only screen and (min-width: 300px) and (max-width: 599px) {
              width: 86%;
            }

            .card-header {
              display: flex;
              justify-content: space-between;
              margin-left: pxToEm(120);
              margin-top: pxToEm(50);
              margin-right: pxToEm(40);
              height: 20%;

              @media only screen and (max-width: 820px) {
                margin: 0;
                height: unset;
              }

              .card-type {
                text-transform: uppercase;
                color: $turquiose;
                font-family: "Arial";
                font-size: pxToEm(40);
                font-weight: 400;

                @media only screen and (max-width: 820px) {
                  font-size: pxToEm(20);
                  font-weight: 600;
                }
              }

              .card-status {
                width: pxToEm(300);
                border: pxToEm(1) solid $turquiose;
                border-radius: pxToEm(20);
                text-transform: uppercase;
                font-family: "Arial";
                align-items: center;
                justify-content: center;
                display: none;

                @media only screen and (max-width: 820px) {
                  width: pxToEm(200);
                }

                span {
                  font-size: pxToEm(22);
                }

                &.is-full {
                  background-color: $white;
                  color: $turquiose;
                  display: flex;

                  span {
                    display: none;
                    font-size: pxToEm(24);

                    &.is-full {
                      display: block;
                    }
                  }
                }

                &.has-vagas {
                  background-color: $turquiose;
                  color: $white;
                  display: flex;

                  span {
                    display: none;
                    font-size: pxToEm(24);

                    &.has-vagas {
                      display: block;
                    }
                  }
                }
              }
            }

            .card-body {
              display: flex;
              justify-content: space-between;
              margin-top: pxToEm(15);
              margin-left: pxToEm(120);
              margin-bottom: pxToEm(50);
              margin-right: pxToEm(40);
              flex-direction: column;
              height: 80%;

              @media only screen and (max-width: 820px) {
                margin: 0;
                margin-top: pxToEm(15);
                height: unset;
                flex-direction: row;
              }

              .card-title {
                margin: 0;
                font-family: "Arial";
                color: #707070;
                font-weight: bold;
                font-size: pxToEm(36);
                max-width: 70%;
                width: 100%;
                height: pxToEm(76);

                @media only screen and (max-width: 820px) {
                  font-size: pxToEm(22);
                  max-width: 65%;
                }
              }

              .card-date {
                display: flex;
                justify-content: space-between;
                width: 100%;
                text-align: right;
                align-items: center;

                @media only screen and (max-width: 820px) {
                  align-items: unset;
                }

                a {
                  width: pxToEm(320);
                  height: pxToEm(55);
                  display: flex;
                  align-items: center;
                  justify-content: center;

                  @media only screen and (max-width: 820px) {
                    position: absolute;
                    width: pxToEm(220);
                    height: pxToEm(55);
                    bottom: pxToEm(-27.5);
                    left: 50%;
                    transform: translateX(-50%);
                  }

                  .card-cta {
                    width: 100%;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background-color: $turquiose;
                    border: pxToEm(1) solid $turquiose;
                    border-radius: pxToEm(20);
                    transition: 0.5s;
                    cursor: pointer;

                    .cta-txt {
                      color: #fff;
                      font-size: pxToEm(22);
                    }

                    &:hover {
                      background-color: $white;

                      .cta-txt {
                        color: $turquiose;
                      }
                    }
                  }
                }

                .txt {
                  width: 100%;
                  margin-right: pxToEm(10);
                  font-size: pxToEm(20);
                }

                .icon {
                  width: pxToEm(95);
                  height: pxToEm(60);
                  background-size: contain;
                  background-repeat: no-repeat;

                  @media only screen and (max-width: 820px) {
                    width: pxToEm(50);
                    height: pxToEm(50);
                  }
                }
              }
            }
          }
        }
      }

      .indicators {
        // height: 100%;
        display: flex;
        width: pxToEm(130);
        justify-content: center;

        @media only screen and (max-width: 820px) {
          margin: pxToEm(20);
          justify-content: center;
        }

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          margin-bottom: pxToEm(40);
        }

        .ind {
          border-radius: $circle;
          width: pxToEm(22);
          height: pxToEm(22);
          background-color: $dk-gray-3;
          cursor: pointer;
          margin: 0 pxToEm(5);

          &.active {
            background-color: $white;
          }
        }
      }
    }
  }
}
</style>



